import { h } from "preact"
import { connect } from "react-redux"
import {
  load_playlist_next,
  add_to_playlist,
  play_song_from_btn
} from "../../store/actions"

import Loading from "../Loading/Loading"
import { SongCard, CardContainer } from "../SongCard/SongCard"
import { InfiniteScroll } from "../InfiniteScroll/InfiniteScroll"

const SongList = ({ state, loadMore, playSong, addToPlaylist }) => {
  const { playlist, loadingPlaylist } = state

  return (
    <InfiniteScroll loadMore={loadMore}>
      <CardContainer>
        {playlist.map((song, index) => (
          <SongCard
            song={{ ...song, index }}
            from="/"
            play={playSong}
            playlistAction={addToPlaylist}
            key={song.id}
          />
        ))}
        <Loading isLoading={loadingPlaylist} loadMore={loadMore} />
      </CardContainer>
    </InfiniteScroll>
  )
}

const state = state => ({
  state: state.root
})

const actions = dispatch => ({
  loadMore: () => dispatch(load_playlist_next()),
  playSong: (index, location) => dispatch(play_song_from_btn(index, location)),
  addToPlaylist: song => dispatch(add_to_playlist(song))
})

export default connect(state, actions)(SongList)



